import React, { FC, ChangeEvent, MouseEvent, useState, useEffect } from "react";
import type { CheckboxValueType } from "antd/es/checkbox/Group";
import { categoriesArr, brandArr, starsArr } from "mockedData/mockedData";
import { calcCategoriesAmount } from "@utils/calcCategoriesAmount";
import { Checkbox, Rate, Slider, InputNumber } from "antd";
import { addFilterValues } from "@store/reducers/UserSlice";
import { IFilterData } from "@models/ICard";
import { useAppDispatch, useAppSelector } from "@hooks/hooks";
import s from "./Categories.module.scss";
import "./checkbox.css";

const Categories: FC = () => {
  const [input, setInput] = useState<IFilterData>({
    category: [],
    brand: [],
    rating: [],
    priceMin: 5,
    priceMax: 100,
  });

  const cards = useAppSelector((state) => state.food.cards);
  const dispatch = useAppDispatch();

  const onChange = (e: number, fieldName: string): void => {
    setInput({ ...input, [fieldName]: e });
  };

  const onClick = (e: number | string, fieldName: string): void => {
    setInput({
      ...input,
      [fieldName]:
        fieldName === "category"
          ? [...input.category, e]
          : fieldName === "brand"
          ? [...input.brand, e]
          : [...input.rating, e],
    });
  };

  const toggleChange = (checked: boolean, id: number, value: number | string, fieldName: string): void => {
    console.log(checked, id, value, fieldName);
    checked &&
      setInput({
        ...input,
        [fieldName]:
          fieldName === "rating" ? input.rating.filter((el) => el !== value) : input.brand.filter((el) => el !== value),
      });
    starsArr[id].checked = !checked;
  };

  console.log(input);

  const multSelect = ([priceMin, priceMax]: Array<number>) => {
    setTimeout(() => {
      onChange(priceMin, "priceMin");
    }, 5);
    setTimeout(() => {
      onChange(priceMax, "priceMax");
    }, 10);
  };

  useEffect(() => {
    input && dispatch(addFilterValues(input));
  }, [input]);

  return (
    <div className={s.sidebar}>
      <div className={s.categories}>
        <h3 className={s.sidebar__topic}>Categories</h3>
        {categoriesArr.map((el) => {
          return (
            <div className={s.categories__item} onClick={() => onClick(el, "category")}>
              <div className={`${s.categories__name} ${s.sidebar__text} `}>{el}</div>
              <div className={s.categories__amount}>{calcCategoriesAmount(cards, el)}</div>
            </div>
          );
        })}
      </div>
      <div className={s.brands}>
        <h3 className={s.sidebar__topic}>Brands</h3>
        {brandArr.map((el) => {
          return (
            <div className={s.brands__item} onClick={() => onClick(el, "brand")}>
              <Checkbox>
                <div className={s.sidebar__text}>{el}</div>
              </Checkbox>
            </div>
          );
        })}
      </div>
      <div className={s.rating}>
        <h3 className={s.sidebar__topic}>Rating</h3>
        {starsArr.map((el, id) => {
          return (
            <div key={id} className={s.brands__item} onClick={() => onClick(el.value, "rating")}>
              <Checkbox checked={el.checked} onChange={() => toggleChange(el.checked, id, el.value, "rating")}>
                <Rate disabled defaultValue={el.value} />
              </Checkbox>
            </div>
          );
        })}
      </div>
      <div className={s.price}>
        <h3 className={s.sidebar__topic}>Price</h3>
        <Slider range min={1} max={100} value={[input.priceMin, input.priceMax]} onChange={(e) => multSelect(e)} />
        <div className={s.sidebar__buttons}>
          <div className={s.price__button}>
            <span> Min</span>{" "}
            <InputNumber
              step={3}
              min={1}
              max={input.priceMax}
              value={input.priceMin}
              onChange={(e) => onChange(e, "priceMin")}
            />
          </div>
          <div className={s.price__button}>
            <span> Max</span>{" "}
            <InputNumber
              step={3}
              min={input.priceMin}
              max={100}
              value={input.priceMax}
              onChange={(e) => onChange(e, "priceMax")}
            />
          </div>
        </div>
      </div>
      <div className={s.reset}>
        <span>Reset</span>
      </div>
    </div>
  );
};

export default Categories;
