import { createSlice } from "@reduxjs/toolkit";
import type { PayloadAction } from "@reduxjs/toolkit";
import type { RootState } from "@store/store";
import { ICard, IFilterData } from "@models/ICard";

interface ShopState {
  cards: Array<ICard>;
  searchValue: string;
  filterValues: IFilterData;
}

const initialState: ShopState = {
  cards: [],
  searchValue: "",
  filterValues: {
    category: [],
    brand: [],
    rating: [],
    priceMin: 20,
    priceMax: 70,
  },
};

export const counterSlice = createSlice({
  name: "counter",

  initialState,
  reducers: {
    setCards: (state, action: PayloadAction<ICard[]>) => {
      state.cards = action.payload;
    },
    addSearchValue: (state, action: PayloadAction<string>) => {
      state.searchValue = action.payload;
    },
    addFilterValues: (state, action: PayloadAction<IFilterData>) => {
      state.filterValues = action.payload;
    },
  },
});

export const { setCards, addSearchValue, addFilterValues } = counterSlice.actions;

export default counterSlice.reducer;
